article {
  nav {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    li {
      background-color:hsl(217, 28%, 15%);
      display: flex;
      border-radius: 10px;
      flex-direction: column-reverse;
      text-align: start;
      margin-bottom: 2rem;
      padding: 1rem;
      img {
        width: 50px;
        border-radius: 50%;
        margin-right: 1rem;
      }
      .comment {
        display: block;
        div {
          display: inline-block;
        }
      }

     
    }
  }
}